/* global document, Office, Word */

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;

    // Update on timer
    setInterval(run, 5000);

    // Update on selection change
    Word.run(async context => {
      context.document.addHandlerAsync(
        Word.EventType.DocumentSelectionChanged,
        run
      );
      await context.sync();
    });
  }
});

export async function run() {
  return Word.run(async (context) => {
    // 1. Get all text in the document body
    const body = context.document.body;
    body.load("text");
    await context.sync();

    const text = body.text;

    // 2. Use regex to find all (number 分) patterns
    //    Example: (3 分), (5 分), etc.
    const regex = /\((\d+)\s*分\)/g;
    let match;
    let total = 0;

    while ((match = regex.exec(text)) !== null) {
      total += parseInt(match[1], 10); // Add the number to total
    }

    // 3. Display the result in the task pane
    //    (Make sure your taskpane.html has an element with id="result")
    document.getElementById("result").textContent = `Total marks: ${total}`;
  });
}